import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Location} from '@angular/common';

import { NgFlashMessageService } from 'ng-flash-messages';
import { CustomerService } from '../../service/customer.service';

import { Customer} from '../../models/customer';

@Component({
  selector: 'app-edit-customer',
  templateUrl: './edit-customer.component.html',
  styleUrls: ['./edit-customer.component.css']
})
export class EditCustomerComponent implements OnInit {
  id: string;
  customer : Customer = {
    firstName : '',
    lastName :'',
    email : '',
    contactNumber : '',
    address : { 
        line1 : '',
        city: '',
        country: ''
    }
  }

  address

  constructor(
    private flashMessage: NgFlashMessageService,
    private customerService: CustomerService,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location    
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.customerService.getCustomer(this.id).subscribe(customer => this.customer = customer);
  }

  onSubmit({value, valid} : {value: Customer, valid: boolean}){ //for now there's no use for VALUE (will check later)
  if(!valid){
    //show error
    this.flashMessage.showFlashMessage({messages: ["Please fill out the form correctly"], type: 'danger', timeout: 4000});
  }else{
    this.customer.id = this.id;
    this.customerService.updateCustomer(this.customer);
    this.flashMessage.showFlashMessage({messages:["Customer updated successfully"], type: 'success', timeout: 4000});
    // this.router.navigate(['/customers']);
    this.location.back();
  }
}

goBack(): void{
  this.location.back();
}

}
