import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Location} from '@angular/common';

import {Item} from '../../models/item';

import { NgFlashMessageService } from 'ng-flash-messages';

import {ItemService} from '../../service/item.service';

import {AngularFireStorage} from '@angular/fire/storage';
import {finalize} from 'rxjs/operators';
import { Observable } from 'rxjs';



@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.scss']
})
export class EditProductComponent implements OnInit {
  id : string;
  item: Item = {
    id: '',
    itemName : '',
    itemDesc : '',
    unitPriceAed : 0,
    unitPricePhp : 0,
    stockStatus : '',
    imagePath: ''
  }

  uploadPercent: Observable<number>;
  downloadURL: Observable<string>;


  constructor(
    private itemService: ItemService,
    private flashMessage: NgFlashMessageService,
    private storage: AngularFireStorage,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location  
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.itemService.getItem(this.id).subscribe(item => this.item = item);
  }

  onSubmit({value, valid} : {value: Item, valid: boolean}){
    console.log("valid: ", valid);
    console.log("value: ", value);
    if(!valid || !value.itemName.trim() || value.unitPriceAed < 1 || value.unitPricePhp < 1){
      this.flashMessage.showFlashMessage({messages:["Please fill-out the form correctly"], type: 'danger', timeout: 4000});
    }else{
      value.id = this.id;
      if(this.item.imagePath){
        value.imagePath = this.item.imagePath;
      }
      console.log("onSubmit: ", value);
      this.itemService.updateItem(value);
      this.flashMessage.showFlashMessage({messages:['Item updated successfully'], type:'success', timeout: 4000});
      this.location.back();
    }
  }

  uploadFile(event){
    const file = event.target.files[0];
    const filePath = `fps/${new Date().getTime()}_${file.name}`;
    const fileRef = this.storage.ref(filePath);
    const task = this.storage.upload(filePath, file);
    
    // TO DO : add a code to check set temp value if empty
    this.item.imagePath = filePath;

    //observe percentage changes
    this.uploadPercent = task.percentageChanges();
    //get notified when the download URL is available
    task.snapshotChanges().pipe(
      finalize(() => this.downloadURL = fileRef.getDownloadURL())
    )
    .subscribe()
  }

  goBack(){
    this.location.back();
  }

}
