import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { map } from 'rxjs/operators';

import {Customer} from '../../models/customer';
import {Invoice} from '../../models/Invoice';

import {CustomerService} from '../../service/customer.service';
import { NgFlashMessageService } from 'ng-flash-messages';
import { InvoiceService } from '../../service/invoice.service';


@Component({
  selector: 'app-customer-details',
  templateUrl: './customer-details.component.html',
  styleUrls: ['./customer-details.component.css']
})
export class CustomerDetailsComponent implements OnInit {
  id : string;
  customer: Customer = {
    id : '',
    firstName: '',
    lastName : '',
    email : '',
    contactNumber: '',
    address : {
      line1 : '',
      city : '',
      country : ''
    } 
  }

  invoices: Invoice[];
  invoiceExist: boolean = false;
  showSpinner : boolean = true;

  constructor(
    private customerService: CustomerService,
    private flashMessage: NgFlashMessageService,
    private router: Router,
    private route: ActivatedRoute,
    private invoiceService: InvoiceService
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.customerService.getCustomer(this.id).subscribe(customer => {
        this.customer = customer;
        this.showSpinner = false;
    });
    this.invoiceService.getInvoices().subscribe(invoices => this.invoices = invoices);
  }

  checkIfInvoiceExist() {
    if(this.invoices){
      this.invoices.map(invoices => {
        if(invoices.customerId == this.id){
          this.invoiceExist = true;
        }  
      });
    }
  }

  onDeleteClick(){
    this.checkIfInvoiceExist();
    if(this.invoiceExist){
      this.flashMessage.showFlashMessage({messages:['Cannot remove customer. An invoice exist under this customer.'], type:'danger', timeout: 4000});
    }else{
      if(confirm("Are you sure?")){
        this.customerService.deleteCustomer(this.id);
        console.log("onDeleteClick: ", this.customer);
        this.flashMessage.showFlashMessage({messages:['Customer removed.'], type:'success', timeout: 4000});
        this.router.navigate(['/customers']);
      }
    }
  }

}
