import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Invoice } from '../models/Invoice';

@Injectable({
  providedIn: 'root'
})
export class InvoiceService {
  invoicesCollection: AngularFirestoreCollection<Invoice>;
  invoiceDoc: AngularFirestoreDocument<Invoice>;
  invoices: Observable<Invoice[]>;
  invoice: Observable<Invoice>;

  constructor(private afs: AngularFirestore) { 
    this.invoicesCollection = this.afs.collection('Invoice', ref=>ref.orderBy('dateInvoiced', 'desc')); //the variable inside afs.collection should match the name in your firestore
  }

  getInvoices(): Observable<Invoice[]>{
    this.invoices = this.invoicesCollection.snapshotChanges()
    .pipe(map(changes => {
      return changes.map(action => {
        const data = action.payload.doc.data() as Invoice;
        const id = action.payload.doc.id;
        return {id, ...data } ;
      });
    }));
    return this.invoices;
  }

  getInvoice(id : string) : Observable<Invoice>{
    this.invoiceDoc = this.afs.doc<Invoice>(`Invoice/${id}`);
    this.invoice = this.invoiceDoc.snapshotChanges().pipe(
      map(action => {
        if(action.payload.exists === false){
          return null;
        }else{
          const data = action.payload.data() as Invoice;
          data.id = action.payload.id;
          return data;
        }
      })
    );
    return this.invoice;
  }

  newInvoice(invoice: Invoice){
      this.invoicesCollection.add(invoice);
  }

  updateInvoice(invoice: Invoice){
    this.invoiceDoc = this.afs.doc(`Invoice/${invoice.id}`);
    this.invoiceDoc.update(invoice);
  }

  deleteInvoice(invoice: Invoice){
    this.invoiceDoc = this.afs.doc(`Invoice/${invoice.id}`);
    this.invoiceDoc.delete();
  }

  deleteItem({invoiceId, itemArrayId}: {invoiceId: string, itemArrayId: string}){
    this.invoiceDoc = this.afs.doc(`Invoice/${invoiceId}`);
    console.log("delete Item Invoice Doc: ", this.invoiceDoc);
  }

}
