import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {Customer} from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {
  customersCollection: AngularFirestoreCollection<Customer>;
  customerDoc: AngularFirestoreDocument<Customer>;
  customers: Observable<Customer[]>;
  customer: Observable<Customer>;

  constructor(private afs:AngularFirestore) {
    this.customersCollection = this.afs.collection('Customer');
   }

   getCustomers(): Observable<Customer[]>{
     this.customers = this.customersCollection.snapshotChanges().pipe(map(
       changes => {
         return changes.map(action => {
           const data = action.payload.doc.data() as Customer;
           const id = action.payload.doc.id;
           return {id, ...data};
         });
       }
     )) ;
     return this.customers;
   }

   getCustomer(id: string): Observable<Customer>{
     this.customerDoc = this.afs.doc<Customer>(`Customer/${id}`);
     this.customer = this.customerDoc.snapshotChanges().pipe(
      map(action => {
        if(action.payload.exists === false){
          return null;
        }else{
          const data = action.payload.data() as Customer;
          return data;
        }
      })
     );
     return this.customer;
   }

   newClient(customer: Customer){
      this.customersCollection.add(customer);
   }

   deleteCustomer(id: string){
     console.log("deleteCustomer Customer ID: ", id);
     this.customerDoc = this.afs.doc(`Customer/${id}`);
     this.customerDoc.delete();
   }

   updateCustomer(customer: Customer){
    this.customerDoc = this.afs.doc(`Customer/${customer.id}`)
    this.customerDoc.update(customer);
   }

}
