import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent } from './components/dashboard/dashboard.component';
import { CustomersComponent } from './components/customers/customers.component';
import { AddCustomerComponent } from './components/add-customer/add-customer.component';
import { EditCustomerComponent } from './components/edit-customer/edit-customer.component';
import { CustomerDetailsComponent } from './components/customer-details/customer-details.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { ShopComponent } from './components/shop/shop.component';
import { HomepageComponent } from './components/homepage/homepage.component';
import { ProductsComponent } from './components/products/products.component';
import { AddProductComponent } from './components/add-product/add-product.component';
import { ProductDetailsComponent } from './components/product-details/product-details.component';
import {EditProductComponent} from './components/edit-product/edit-product.component';
import { ShoppingCartComponent } from './components/shopping-cart/shopping-cart.component';
import { OrdersComponent } from './components/orders/orders.component';
import { OrderDetailsComponent } from './components/order-details/order-details.component';
import {AddInvoiceComponent} from './components/add-invoice/add-invoice.component';
import { ReportComponent } from './components/report/report.component';
import { EditOrdersComponent } from './components/edit-orders/edit-orders.component';
import {SettingsComponent} from './components/settings/settings.component';
import { NotfoundComponent } from './components/notfound/notfound.component';

import { AuthGuard } from './guards/auth.guard';
import {RegisterGuard} from './guards/register.guard';



const routes: Routes = [
  { path: ''                      ,  redirectTo: '/orders', pathMatch: 'full' , canActivate:[AuthGuard] },
  { path: 'customers'             ,  component:  CustomersComponent           , canActivate:[AuthGuard] },
  { path: 'customer/add'          ,  component:  AddCustomerComponent         , canActivate:[AuthGuard] },
  { path: 'customer/edit/:id'     ,  component:  EditCustomerComponent        , canActivate:[AuthGuard] },
  { path: 'customer/:id'          ,  component:  CustomerDetailsComponent     , canActivate:[AuthGuard] },
  { path: 'login'                 ,  component:  LoginComponent },
  { path: 'register'              ,  component:  RegisterComponent            , canActivate:[RegisterGuard]}, 
  { path: 'shop'                  ,  component:  ShopComponent                , canActivate:[AuthGuard] },
  { path: 'home'                  ,  component:  HomepageComponent            , canActivate:[AuthGuard] },
  { path: 'products'              ,  component:  ProductsComponent            , canActivate:[AuthGuard] },
  { path: 'product/add'           ,  component:  AddProductComponent          , canActivate:[AuthGuard] },
  { path: 'product/:id'           ,  component:  ProductDetailsComponent      , canActivate:[AuthGuard] },
  { path: 'product/edit/:id'      ,  component:  EditProductComponent         , canActivate:[AuthGuard] },
  { path: 'cart'                  ,  component:  ShoppingCartComponent        , canActivate:[AuthGuard] },
  { path: 'orders'                ,  component:  OrdersComponent              , canActivate:[AuthGuard] },
  { path: 'order/:id'             ,  component:  OrderDetailsComponent        , canActivate:[AuthGuard] },
  { path: 'invoice/add'           ,  component:  AddInvoiceComponent          , canActivate:[AuthGuard] },
  { path: 'report'                ,  component:  ReportComponent              , canActivate:[AuthGuard] },
  { path: 'order/edit/:id'        ,  component:  EditOrdersComponent          , canActivate:[AuthGuard] },
  { path: 'settings'              ,  component:  SettingsComponent            , canActivate:[AuthGuard] },
  { path: '**'                    ,  component:  NotfoundComponent}
]

@NgModule({
  exports: [RouterModule],
  imports: [
    RouterModule.forRoot(routes)
  ],
  providers: [AuthGuard, RegisterGuard]
})
export class AppRoutingModule { }
