
import { Component, OnInit} from '@angular/core';

import {InvoiceService } from '../../service/invoice.service';
import {CustomerService} from '../../service/customer.service';
import {ItemService} from '../../service/item.service';

import { Invoice } from '../../models/Invoice';
import {Customer} from '../../models/customer';
import {Item} from '../../models/item';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit{

  invoices: Invoice[];
  invoice: Invoice;
  customerName: string = '';
  customerCountry: string = '';
  customers: Customer[] = [];
  items: Item[] = [];
  itemTotalAed : number;
  itemTotalPhp : number;
  totalAmountPaid: number = 0;
  totalAmountDue: number = 0;
  hasBalance : boolean;
  showSpinner : boolean = true;

  pendingInvoices : Invoice[] = [];
  deliveredInvoices : Invoice [] = [];

  dtOptions : DataTables.Settings = {};

  constructor(
    private invoiceService: InvoiceService,
    private customerService: CustomerService,
    private itemService: ItemService
    ) { }


  ngOnInit() {
    this.invoiceService.getInvoices().subscribe(invoices => {
       this.invoices = invoices;
       this.showSpinner = false;
    });
    this.customerService.getCustomers().subscribe(customers => this.customers = customers);
    this.itemService.getItems().subscribe(items => this.items = items);
    this.dtOptions = {
      pagingType : 'full_numbers'
    }
  }

  findCustomer(id: string){
    this.customers.forEach(customer => {
      if(customer.id === id){
        this.customerName = customer.firstName + ' ' + customer.lastName;
        this.customerCountry = customer.address.country;
      }
    });
  }

  getTotal(invoice: Invoice) {
    this.itemTotalAed = 0;
    this.itemTotalPhp = 0;
    if(invoice.listItemArray.length > 0){
      invoice.listItemArray.forEach(listItem =>{  //loop through the list Item Array
        this.items.forEach(item => {  // loop through the Item Collection
          if(item.id === listItem.itemId){
            this.itemTotalAed = this.itemTotalAed + (item.unitPriceAed * listItem.quantity);
            this.itemTotalPhp = this.itemTotalPhp + (item.unitPricePhp * listItem.quantity);
          }
        })
      });
      this.itemTotalAed = this.itemTotalAed + invoice.freight - invoice.discount;
    }
  };


    getBalance(invoice: Invoice){
      this.totalAmountPaid = 0;
      this.totalAmountDue = 0;
      if(invoice.payments.length > 0){
        invoice.payments.forEach(payment => {
          this.totalAmountPaid = this.totalAmountPaid + payment.amount;
        });
      }

      this.totalAmountDue = this.itemTotalAed - this.totalAmountPaid;
      if(this.totalAmountDue > 0){
        this.hasBalance = true;
      }else{
        this.hasBalance = false;
      }
    }

}
