import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { NgFlashMessageService } from 'ng-flash-messages';
import { InvoiceService } from '../../service/invoice.service';
import {CustomerService} from '../../service/customer.service';
import{ ItemService } from '../../service/item.service';

import {Invoice} from '../../models/Invoice';
import {Customer} from '../../models/customer';
import {Item} from '../../models/item';
import {Address} from '../../models/customer';

@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html',
  styleUrls: ['./order-details.component.css']
})
export class OrderDetailsComponent implements OnInit {
  id : string;
  customers: Customer[] = [];
  customerName: string = "";
  customer: Customer = {};
  items: Item[] = [];
  itemTotalAed : number = 0;
  totalAmountPaidAed: number = 0;
  balanceAed: number = 0;
  hasBalance : boolean = false;
  item: Item = {};
  address: Address = {};
  lineTotal : number = 0;
  subTotal : number = 0;
  invoice : Invoice = {
    id : '',
    invoiceNumber : '',
    customerId : '',
    discount : 0,
    freight : 0,
    status : '',
    dateInvoiced : 0,
    dateDelivered : 0,
    deliveryOption : '',
    remarks : '',
    listItemArray : [],
    payments : []
  }

  showSpinner : boolean = true;

  constructor(
    private invoiceService: InvoiceService,
    private flashMessage: NgFlashMessageService,
    private router: Router,
    private route: ActivatedRoute,
    private customerService: CustomerService,
    private itemService : ItemService
    ) { }

  ngOnInit() {
    //Get id from url
    this.id = this.route.snapshot.params['id'];
    this.invoiceService.getInvoice(this.id).subscribe(invoice => {
      this.invoice = invoice;
      this.showSpinner = false;
    
    });
    this.customerService.getCustomers().subscribe(customers => this.customers = customers);
    this.itemService.getItems().subscribe( items => this.items = items);
  }

  findCustomer(){
    this.customers.forEach(customer => {
      if(customer.id === this.invoice.customerId){
        this.customer = customer;
        this.address = customer.address;
        this.customerName = customer.firstName + ' ' + customer.lastName;
      }
    });
  }

  getTotalAmount(){
    this.itemTotalAed = 0;
    this.subTotal = 0;
    this.invoice.listItemArray.forEach(listItem => {
      this.items.forEach(item => {
        if(item.id === listItem.itemId){
            this.subTotal = this.subTotal + (listItem.quantity * item.unitPriceAed);
        }
      });
    });
    this.itemTotalAed = this.subTotal + this.invoice.freight - this.invoice.discount;
  }

  getBalance(){
    this.totalAmountPaidAed = 0;
    if(this.invoice.payments.length > 0){
      this.invoice.payments.forEach(payment => {
        this.totalAmountPaidAed = this.totalAmountPaidAed + payment.amount;
      });
    }
    this.getTotalAmount();
    this.balanceAed = this.itemTotalAed - this.totalAmountPaidAed;
    if(this.balanceAed > 0){
      this.hasBalance = true;
    }else{
      this.hasBalance = false;
    }
  }

  findItem(id: string){
    this.items.forEach(item => {
      if(item.id === id){
        this.item = item;
      }
    });
  }

  onDeleteClick(){
    if(confirm('Are you sure?')){
      this.invoiceService.deleteInvoice(this.invoice);
      this.flashMessage.showFlashMessage({messages:['Invoice removed.'], type:'success', timeout: 4000});
      this.router.navigate(['/orders']);
    }
  }

  getLineTotal(quantity: number, price: number){
    this.lineTotal = quantity * price;
  }

}
