import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {Item} from '../models/item';

@Injectable({
  providedIn: 'root'
})
export class ItemService {
  itemCollection : AngularFirestoreCollection<Item>;
  itemDoc: AngularFirestoreDocument<Item>;
  items: Observable<Item[]>;
  item: Observable<Item>;

  constructor(private afs: AngularFirestore) { 
    this.itemCollection = this.afs.collection('Item');
  }

  getItems(): Observable<Item[]> {
    this.items = this.itemCollection.snapshotChanges().pipe(
      map( changes => {
        return changes.map(action => {
          const data = action.payload.doc.data() as Item;
          const id = action.payload.doc.id;
          return {id, ...data};
        });
      })
    );
    return this.items;
  }

  getItem(id: string): Observable<Item>{
    this.itemDoc = this.afs.doc<Item>(`Item/${id}`);
    this.item = this.itemDoc.snapshotChanges().pipe(
      map(action=>{
        if(action.payload.exists === false){
          return null;
        }else{
          const data = action.payload.data() as Item;
          return data;
        }
      })
    );
    return this.item;
  }

  deleteItem(item: Item){
    this.itemDoc = this.afs.doc(`Item/${item.id}`);
    this.itemDoc.delete();
  }

  updateItem(item: Item){
    this.itemDoc = this.afs.doc(`Item/${item.id}`);
    this.itemDoc.update(item);
  }

  addItem(item: Item){
    this.itemCollection.add(item);
  }

}
