import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { NgFlashMessageService } from 'ng-flash-messages';
import { InvoiceService } from '../../service/invoice.service';
import {CustomerService} from '../../service/customer.service';
import{ ItemService } from '../../service/item.service';

import {Invoice, ListItem, Payment} from '../../models/Invoice';
import {Customer} from '../../models/customer';
import {Item} from '../../models/item';
import {Address} from '../../models/customer';

@Component({
  selector: 'app-edit-orders',
  templateUrl: './edit-orders.component.html',
  styleUrls: ['./edit-orders.component.css']
})
export class EditOrdersComponent implements OnInit {

  id: string;
  customer: Customer;
  customers: Customer[];
  items: Item[] = [];
  displayItem : Item = {
    id: '',
    itemName : '',
    itemDesc : '',
    unitPriceAed : 0,
    unitPricePhp : 0,
    stockStatus : ''
  };
  
  indexToDelete : number;
  paymentIndexToDelete: number;
  duplicateFound: boolean = false;
  paymentArray: Payment[] = [];
  payment: Payment = {
    amount : 0,
    datePaid : Date.now(),
    method : ''
  };
  itemTotalAed : number = 0;
  totalAmountPaidAed : number = 0;
  balanceAed : number = 0;
  hasBalance : boolean = true;
  listItem: ListItem = {
    itemId : '',
    quantity : 0
  }
  invoice: Invoice = {
    invoiceNumber: '',
    customerId : '',
    discount : 0,
    freight : 0,
    status : '',
    dateInvoiced : Date.now(),
    dateDelivered : Date.now(),
    deliveryOption: '',
    remarks : '',
    listItemArray :  [],
    payments : []
  }

  //Item To Edit Modal Variables
  itemNameToEdit: string = '';
  listItemToEdit: ListItem = {
    itemId: '',
    quantity: 0
  }

  paymentToEdit: Payment = {
    amount : 0,
    datePaid : 0,
    method : ''
  };
  paymentToEditPosition = 0;

  constructor(
    private invoiceService: InvoiceService,
    private flashMessage: NgFlashMessageService,
    private router: Router,
    private route: ActivatedRoute,
    private customerService: CustomerService,
    private itemService : ItemService
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.invoiceService.getInvoice(this.id).subscribe(invoice => this.invoice = invoice);
    this.customerService.getCustomers().subscribe(customers => this.customers = customers);
    this.itemService.getItems().subscribe( items => this.items = items);
  }

  onSubmit({value, valid} : {value: Invoice, valid: boolean}){
    if(!valid){
      this.flashMessage.showFlashMessage({messages:["Please fill-out the form correctly"], type: 'danger', timeout: 4000});
    }else{
      value.id = this.id;
      value.listItemArray = this.invoice.listItemArray;
      value.payments = this.invoice.payments;
      if(value.deliveryOption == 'Meet-up'){
        value.freight = 0;
      }
      this.invoiceService.updateInvoice(value);
      this.flashMessage.showFlashMessage({messages:["Invoice updated successfully"], type: 'success', timeout:4000});
      this.router.navigate(['/order/' + this.id]);
    }
  }

  findCustomer(){
    this.customers.forEach(customer => {
      if(customer.id === this.invoice.customerId){
        this.customer = customer;
      }
    });
    console.log("Customer: ", this.customer);
  }

  addItem(){
    this.duplicateFound = false;
    if(this.listItem.quantity < 1  || !this.listItem.itemId){
      // clear the list item
      this.listItem = {
        itemId : '',
        quantity : 0,
      }
      this.flashMessage.showFlashMessage({messages: ["Please enter item correctly"], type: 'danger', timeout: 4000});
    }else{
      this.invoice.listItemArray.forEach( item => {
        if(item.itemId === this.listItem.itemId){
          this.duplicateFound = true;
        }
      });
      if(this.duplicateFound){
        this.flashMessage.showFlashMessage({messages: ["Duplicate item."], type: 'danger', timeout: 4000});
      }else{
        this.invoice.listItemArray.push(this.listItem);
        this.flashMessage.showFlashMessage({messages: ["Item added successfully"], type: 'success', timeout: 4000});
        // clear the list item
        this.listItem = {
          itemId : '',
          quantity : 0,
        }
      } 
    }

  }

  getItemDetails(id : string){
    this.items.forEach( itemDetails => {
      if(itemDetails.id == id){
        this.displayItem = itemDetails;
      }
    });
  }

  deleteItem(id : string){
    if(confirm("Are you sure?")){
      this.indexToDelete = this.invoice.listItemArray.findIndex(itemToDelete => itemToDelete.itemId == id);
      this.invoice.listItemArray.splice(this.indexToDelete, 1);
      this.flashMessage.showFlashMessage({messages :["Item removed"], type: 'success', timeout: 4000});
    }
  }

  addPayment(){
    console.log("Add Payment: ", this.payment);
    if(this.payment.amount < 1 || !this.payment.method){
      // clear the payment
      this.payment = {
        amount : 0,
        datePaid : Date.now(),
        method : ''
      }
      this.flashMessage.showFlashMessage({messages: ["Please enter payment correctly"], type: 'danger', timeout: 4000});
    }else{
      this.payment.datePaid = Date.now();
      this.invoice.payments.push(this.payment);
      console.log("Add Payment invoice: ",this.invoice.payments );
      this.flashMessage.showFlashMessage({messages: ["Payment added successfully"], type: 'success', timeout: 4000});
      this.payment = {
        amount : 0,
        datePaid : Date.now(),
        method : ''
      }
    }
  }

  getTotalAmount(){
    this.itemTotalAed = 0;
    this.invoice.listItemArray.forEach(listItem => {
      this.items.forEach(item => {
        if(item.id === listItem.itemId){
            this.itemTotalAed = this.itemTotalAed + (listItem.quantity * item.unitPriceAed);
        }
      });
    });
    this.itemTotalAed = this.itemTotalAed + this.invoice.freight - this.invoice.discount;
  }

  getBalance(){
    this.totalAmountPaidAed = 0;
    this.invoice.payments.forEach(payment => {
      this.totalAmountPaidAed = this.totalAmountPaidAed + payment.amount;
    });
    this.getTotalAmount();
    this.balanceAed = this.itemTotalAed - this.totalAmountPaidAed;
    if(this.balanceAed > 0){
      this.hasBalance = true;
    }else{
      this.hasBalance = false;
    }
  }

  deletePayment(paymentToDelete : Payment){
    if(confirm("Are you sure?")){
      this.paymentIndexToDelete = this.invoice.payments.findIndex(payment => 
        payment.datePaid == paymentToDelete.datePaid
      );
      this.invoice.payments.splice(this.paymentIndexToDelete, 1);
      this.flashMessage.showFlashMessage({messages: ["Payment removed"], type: 'success', timeout: 4000});
    }
  }

  editItem(listItem: ListItem){

    this.listItemToEdit = {...listItem};
    this.items.map(item => {
      if(item.id == listItem.itemId){
        this.itemNameToEdit = item.itemName;
      }
    })
  }

  updateItem(){

    var {quantity, itemId} = this.listItemToEdit;

    if(!quantity || quantity < 1){
      this.flashMessage.showFlashMessage({messages: ["Please enter a valid quantity"], type: 'danger', timeout:4000});
    }else{
      this.invoice.listItemArray.map( listItem => {
        if(listItem.itemId == itemId){
          listItem.quantity = quantity;
        }
      })
    }
  }

  editPayment(paymentToEdit : Payment){
    this.paymentToEdit = {...paymentToEdit};
  }

  updatePayment(){
    var { amount, datePaid, method } = this.paymentToEdit;

    if(!amount || amount < 1 || !method || method.length < 3){
      this.flashMessage.showFlashMessage({messages: ["Please fill out the payment details correctly"], type: 'danger', timeout: 4000});
    }else{
      //console.log({amount, datePaid, method})
      this.invoice.payments.map(payment => {
          if(payment.datePaid == datePaid){
            payment.amount = amount;
            payment.datePaid = datePaid;
            payment.method = method;
          }
        });
    }
    
  
    //console.log(...this.invoice.payments);
  }
}
