import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Location} from '@angular/common';

import {Item} from '../../models/item';
import {ItemService} from '../../service/item.service';

import { NgFlashMessageService } from 'ng-flash-messages';

import {finalize} from 'rxjs/operators';
import { Observable } from 'rxjs';
import {AngularFireStorage} from '@angular/fire/storage';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.scss']
})
export class AddProductComponent implements OnInit {

  itemToAdd: Item = {
    id: '',
    itemName : '',
    itemDesc : '',
    unitPriceAed : 0,
    unitPricePhp : 0,
    stockStatus : '',
    imagePath: '',
  }

  uploadPercent: Observable<number>;
  downloadURL: Observable<string>;


  constructor(
    private itemService: ItemService,
    private flashMessage: NgFlashMessageService,
    private storage: AngularFireStorage,
    private router: Router,
    private route: ActivatedRoute,
    private location: Location  
  ) { }

  ngOnInit() {
  }

  onSubmit({value, valid} : {value: Item, valid: boolean}){
    if(!valid || !value.itemName.trim() || value.unitPriceAed < 1 || value.unitPricePhp < 1){
        this.flashMessage.showFlashMessage({messages:['Please fill-out the form correctly'], type:'danger', timeout: 4000});
    }else{
        if(this.itemToAdd.imagePath){
          value.imagePath = this.itemToAdd.imagePath;
        }
        this.itemService.addItem(value);
        this.flashMessage.showFlashMessage({messages:['Item added successfully.'], type:'success', timeout: 4000});
        //reset values after add
        this.itemToAdd.itemName = '';
        this.itemToAdd.itemDesc = '';
        this.itemToAdd.unitPriceAed = 0;
        this.itemToAdd.unitPricePhp = 0;
        this.itemToAdd.stockStatus = '';
        this.location.back();
      }
  }

  uploadFile(event){
    const file = event.target.files[0];

    const filePath = `image/${new Date().getTime()}_${file.name}`;
    
    this.itemToAdd.imagePath = filePath;

    const fileRef = this.storage.ref(filePath);
    const task = this.storage.upload(filePath, file);
    
    // TO DO : add a code to check set temp value if empty
    
     

    //observe percentage changes
    this.uploadPercent = task.percentageChanges();
    //get notified when the download URL is available
    task.snapshotChanges().pipe(
      finalize(() => this.downloadURL = fileRef.getDownloadURL())
    )
    .subscribe()
  }

  goBack(){
    this.location.back();
  }


}
