import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import {Location} from '@angular/common';

import { NgFlashMessageService } from 'ng-flash-messages';
import { CustomerService } from '../../service/customer.service';

import { Customer} from '../../models/customer';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  customer : Customer = {
    firstName : '',
    lastName :'',
    email : '',
    contactNumber : '',
    address : { 
        line1 : '',
        city: '',
        country: ''
    }
  }


  @ViewChild('customerForm') form: any;

  constructor(private flashMessage: NgFlashMessageService,
              private customerService: CustomerService,
              private router: Router,
              private route: ActivatedRoute,
              private location: Location          
    ) { }

  ngOnInit() {
  }

  onSubmit({value, valid} : {value: Customer, valid: boolean}){ //for now there's no use for VALUE (will check later)
    if(!valid){
      //show error
      this.flashMessage.showFlashMessage({messages: ["Please fill out the form correctly"], type: 'danger', timeout: 4000});
    }else{
      this.customerService.newClient(this.customer);
      this.flashMessage.showFlashMessage({messages:["New Client added"], type: 'success', timeout: 4000});
      // this.router.navigate(['/customers']);
      this.location.back();
    }
  }

  goBack(): void{
    this.location.back();
  }

}
