import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';

import {Item} from '../../models/item';
import {Invoice} from '../../models/Invoice';

import { NgFlashMessageService } from 'ng-flash-messages';

import {Router, ActivatedRoute, Params} from '@angular/router';

import {ItemService} from '../../service/item.service';
import {InvoiceService} from '../../service/invoice.service';

import {AngularFireStorage, AngularFireStorageReference} from '@angular/fire/storage';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.css']
})
export class ProductDetailsComponent implements OnInit {
  id : string;
  item: Item = {
    id: '',
    itemName: '',
    itemDesc: '',
    unitPriceAed:0,
    unitPricePhp: 0,
    stockStatus: '',
    imagePath: ''
  }

  invoices: Invoice[] = [];
  itemExist : boolean = false;
  itemImage : Observable<string> = null;
  ref : AngularFireStorageReference;

  showSpinner : boolean = true;
  
  constructor(
    private itemService: ItemService,
    private flashMessage: NgFlashMessageService,
    private router: Router,
    private route: ActivatedRoute,
    private invoiceService: InvoiceService,
    private storage: AngularFireStorage
  ) { }

  ngOnInit() {
    this.id = this.route.snapshot.params['id'];
    this.itemService.getItem(this.id).subscribe(item => {
        this.item = item;
        this.showSpinner = false;
      });
    this.invoiceService.getInvoices().subscribe(invoices => this.invoices = invoices);
  }

  itemExistInInvoice() {
    this.invoices.map(invoice => {
      invoice.listItemArray.map(listItem => {
        if(listItem.itemId == this.id){
          this.itemExist = true;
        }
      });
    });
  }

  onDeleteClick(){
    this.itemExistInInvoice();
    if(this.itemExist){
      this.flashMessage.showFlashMessage({messages:['Cannot remove item. Item is used in invoice'], type:'danger', timeout: 4000});
    }else{
      if(confirm("Are you sure?")){
        this.item.id = this.id;
        this.itemService.deleteItem(this.item);
       // this.deleteImage();
        this.flashMessage.showFlashMessage({messages:['Item removed.'], type:'success', timeout: 4000});
        this.router.navigate(['/products']);
      }
    }
  }

  displayImage(){
    if(this.item.imagePath){
      this.ref = this.storage.ref(this.item.imagePath);
      this.ref.getDownloadURL().subscribe(url => {
        this.itemImage = url;
        })
    }else{
      this.itemImage = null;
    }
    
  }


  // deleteImage() {
  //   this.storage.ref(this.item.imagePath).delete();
  // }

}
    

